import React, { useState, useEffect, Component } from 'react'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import { fade, makeStyles } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import SearchIcon from '@material-ui/icons/Search';
import Button from '@material-ui/core/Button';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Badge from '@material-ui/core/Badge';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import Tooltip from '@material-ui/core/Tooltip';
import CookieService from '../../API/Cookie'
import { useHistory } from 'react-router-dom';

import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
        display: 'none',
        [theme.breakpoints.up('sm')]: {
            display: 'block',
        },
    },
    search: {
        marginRight: theme.spacing(1),
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(1),
            width: 'auto',
        },
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: '12ch',
            '&:focus': {
                width: '20ch',
            },
        },
    },
    hbutton: {
        marginRight: theme.spacing(1),
        marginLeft: theme.spacing(1),
        color: '#fffff0',
        textTransform: 'none',
        backgroundColor: '#3c52b2',
    }
});

const initialState = {
    user: false,
    type: '',
    name: '',
}

class Header extends Component {

    // const[user, setUser] = useState()
    // const[type, setType] = useState()
    // const[name, setName] = useState()
    // const history = useHistory();


    constructor(props) {
        super(props);
        this.state = initialState;

        const token = CookieService.get()
        if (!token) {
            // setUser(false)
        } else {
            // setUser(true)
            axios.get("http://localhost:4000/api/v1/profile", { withCredentials: true })
                .then(Response => {
                    // setName(Response.data.user.fullName)
                    console.log('done');
                }).catch(error => {
                    alert('false')
                    console.log(Response.data.message);
                    alert('Error ' + Response.message);
                })
            toast.success(`Welcome ${name}`)
        }

    }

    logout() {
        CookieService.remove();
        toast.success(`Logout Success`)
        history.push('/')
        // setUser("")
    }

    render() {
        const { classes } = this.props;
        return (
            <div className={classes.root}>
                <AppBar position="fixed">
                    <ToastContainer />
                    <Toolbar>
                        <Typography className={classes.title} variant="h4" noWrap>
                            <Link to={'/'} style={{ textDecoration: "none", color: "#fffff0" }}>
                                Black Code
                            </Link>
                        </Typography>
                        <a href='#hero'><Button className={classes.hbutton}>Home</Button></a>
                        <a href='#about'><Button className={classes.hbutton}>About Us</Button></a>
                        <a href='#team'><Button className={classes.hbutton}>Team</Button></a>
                        <a href='#contactus'><Button className={classes.hbutton}>Contact Us</Button></a>
                        <a href='#leatetnews'><Button className={classes.hbutton}>Leatet Sessions</Button></a>
                        <a href='#callforpapers'><Button className={classes.hbutton}>Research Papers</Button></a>
                        <a href='#leatetnews'><Button className={classes.hbutton}>Submit Research Papers</Button></a>
                        <a href='#leatetnews'><Button className={classes.hbutton}>Create Sessions</Button></a>
                        <div className={classes.search}>
                            <div className={classes.searchIcon}>
                                <SearchIcon />
                            </div>
                            <InputBase
                                placeholder="Search…"
                                classes={{
                                    root: classes.inputRoot,
                                    input: classes.inputInput,
                                }}
                                inputProps={{ 'aria-label': 'search' }}
                            />
                        </div>
                        {!this.state.user ?
                            <div>
                                <Link to={'/signin'} style={{ textDecoration: "none", color: "#fffff0" }}><Button color="inherit">Sign In</Button></Link>
                                <Link to={'/signup'} style={{ textDecoration: "none", color: "#fffff0" }}><Button color="inherit">Sign Up</Button></Link>
                            </div>
                            :
                            <div>
                                <Link style={{ textDecoration: "none", color: "#fffff0" }}>
                                    <Button size="medium" style={{ width: "150px" }} color="inherit">
                                        User
                                    </Button>
                                </Link>
                                <Tooltip title="Notifications">
                                    <IconButton aria-label="show 17 new notifications" color="inherit">
                                        <Badge badgeContent={17} color="secondary">
                                            <NotificationsIcon />
                                        </Badge>
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title="Logout">
                                    <IconButton
                                        onClick={logout}
                                        color="inherit">
                                        <ExitToAppIcon />
                                    </IconButton>
                                </Tooltip>
                            </div>
                        }
                    </Toolbar>
                </AppBar>
            </div>
        );
    }
}

export default withStyles(styles, { withTheme: true })(Header);