import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';

const AddressForm = () => {
    return (
        <React.Fragment>
            <Typography variant="h6" gutterBottom>
                Personal Data
  </Typography>
            <Grid container spacing={3}>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="firstName"
                        name="firstName"
                        label="First name"
                        fullWidth
                        autoComplete="given-name" />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="lastName"
                        name="lastName"
                        label="Last name"
                        fullWidth
                        autoComplete="family-name" />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="fullName"
                        name="FullName"
                        label="Full Name"
                        fullWidth
                        autoComplete="full name" />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="birthday"
                        name="firstName"
                        label="Birthday"
                        fullWidth
                        autoComplete="given-name" />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="lastName"
                        name="lastName"
                        label="Gender"
                        fullWidth
                        autoComplete="family-name" />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="address1"
                        name="address1"
                        label="Email"
                        fullWidth
                        autoComplete="shipping address-line1" />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="address2"
                        name="address2"
                        label="Password"
                        fullWidth
                        autoComplete="shipping address-line2" />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="address2"
                        name="address2"
                        label="Profile Picture"
                        fullWidth
                        autoComplete="shipping address-line2" />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="city"
                        name="city"
                        label="User Type"
                        fullWidth
                        autoComplete="shipping address-level2"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="city"
                        name="city"
                        label="Contact Number"
                        fullWidth
                        autoComplete="shipping address-level2"
                    />
                </Grid>

                <Grid item xs={12}>
                    <TextField
                        required
                        id="address11"
                        name="address2"
                        label="Address 1"
                        fullWidth
                        autoComplete="shipping address-line2" />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        required
                        id="address22"
                        name="address2"
                        label="Address 2"
                        fullWidth
                        autoComplete="shipping address-line2" />
                </Grid>

                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="city"
                        name="city"
                        label="City"
                        fullWidth
                        autoComplete="shipping address-level2"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        id="state"
                        name="state"
                        label="State/Province/Region"
                        fullWidth />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="zip"
                        name="zip"
                        label="Zip / Postal code"
                        fullWidth
                        autoComplete="shipping postal-code"
                    />
                </Grid>
                <Grid item xs={12} sm={6}>
                    <TextField
                        required
                        id="country"
                        name="country"
                        label="Country"
                        fullWidth
                        autoComplete="shipping country"
                    />
                </Grid>
            </Grid>
        </React.Fragment>
    )
}

export default AddressForm
